import { mapGetters } from 'vuex'
import { GChart } from 'vue-google-charts'
export default {
  name: 'relatorio',
  components: {
    GChart
  },
  data: () => {
    return {
      chartOptions: {
        width: '100%',
        legend: { position: 'top' },
        axes: {
          x: {
            0: { side: 'top', label: 'Tipos' }
          }
        },
        bar: { groupWidth: '40%' },
        colors: ['#EA4335', '#34A853', '#4285f4', '#ADB3BA']
      }
    }
  },
  computed: {
    ...mapGetters([
      'resultado',
      'definicoesPalavras',
      'personalidades',
      'formulario'
    ]),
    chartData () {
      return [
        ['Resultados', 'Sanguíneo', 'Colérico', 'Melancólico', 'Fleumático'],
        ['', this.resultado[0], this.resultado[1], this.resultado[2], this.resultado[3]]
      ]
    },
    returnTipo () {
      let max = Math.max(this.resultado[0], this.resultado[1], this.resultado[2], this.resultado[3])
      if (max === this.resultado[0]) {
        return 'A Personalidade do Popular Sanguíneo. O Extrovertido, o Falador, o Otimista'
      }
      if (max === this.resultado[1]) {
        return 'A Personalidade do Forte Colérico. O Extrovertido, o Fazedor, o Otimista'
      }
      if (max === this.resultado[2]) {
        return 'A Personalidade do Perfeito Melancólico. O Introvertido, o Pensador, o Pessimista'
      }
      if (max === this.resultado[3]) {
        return 'A Personalidade do Sereno Fleugmático. O Introvertïdo, o Observador, o Pessimista'
      }
      return ''
    }
  },
  mounted () {
    setTimeout(() => {
      localStorage.setItem('inicio', false)
      localStorage.removeItem('formulario')
    }, 10000)
  },
  created () {
    this.$store.commit('formulario', JSON.parse(localStorage['formulario']))
  }
}
